@import url(https://fonts.googleapis.com/css2?family=Bodoni+Moda&family=Poppins:wght@300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
img {
  width: 300px;
  height: 300px;
  transition: all 0.3s ease;
}

img:hover {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
button {
  padding: 10px;
  background: black;
  color: white;
  width: 300px;
  font-size: 20px;
  border-radius: 5px;
}
.book-details {
  min-height: 80vh;
  width: 100%;
  font-size: 20px;
  display: flex;
  align-items: center;
  justify-content: space-evenly;
  padding: 10px;
}

.book-image {
  margin: 0 30px;
}

.book-description {
  margin: 0x 20px;
}
.book{
  font-size: 25px;
  font-weight: bold;
  color: aliceblue;
  margin-top: 20px;

}
.booklist {
    min-height: 80vh;
    width: 100%;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    justify-items: center;
    text-align: center;
  }
.navbar {
    width: 100%;
    height: 10vh;
    color: white;
    display: flex;
    align-items: center;
    justify-content: space-evenly;
  }
  .navbar-element h3{
      font-size: 25px;
      color: white;
      font-weight: bold;
      text-decoration: none;
      display: flex;
  }

.navbar-content{
    font-size: 20px;
    font-family: 'Poppins', sans-serif;
    margin-left: auto;
}

.navbar-content ul {
    display: flex;
    padding: 0;
}
.navbar-content li{
    list-style-type: none;
    font-weight: bold;
    margin: 0 1rem ;

}
.navbar-content a{
    text-decoration: none;
    color: white;
    display: flex;
    width: 100%;
}


.footer {
    width: 100%;
    height: 10vh;
    background: black;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
*{
    padding: 0;
    margin: 0;
    font-family: 'Poppins', sans-serif;
}
:root{
    --primary-color:#755139FF;
    --secondary-color:#D4B996FF;
}
body{
    background-color: #755139FF;
    background-color: var(--primary-color);
}
html{
    font-size: 75.5%;
}
.header-section{
    width: 100%;
    height: 500px;
    color: #D4B996FF;
    color: var(--secondary-color);
    box-shadow: 0 5px 10px #D4B996FF;
    box-shadow: 0 5px 10px var(--secondary-color);
    align-items: center;
    display: flex;
}
.search-section{
    position: absolute;
    top:50px;
    right: 150px;
    text-align: center;
}
.search-section h2{
    font-size: 3rem;
    margin-top: 60px;
    margin-bottom: 10px;

}
.search-section img{
    margin-top: 30px;
    width: 300px;
}

